section {
  @media screen and(max-width:767px) {
    padding-bottom: 40px;
  }
  @include tablet {
    padding-bottom: 60px;
  }
  @include desktop {
    padding-bottom: 40px;
  }
}

.container {
  padding-left: 20px;
  padding-right: 20px;
  margin-left: auto;
  margin-right: auto;
  @include tablet {
    width: 768px;
    padding-left: 16px;
    padding-right: 16px;
  }
  @include desktop {
    width: 1200px;
  }
}
