::-webkit-scrollbar-thumb {
  background: var(--accent-color);
  border-radius: 10px;
}

::-webkit-scrollbar-thumb:window-inactive {
  background: var(--accent-color);
}

#progress {
  height: 70px;
  width: 70px;
  z-index: 105;
  cursor: pointer;
  border-radius: 50%;
  align-items: center;
  justify-items: center;
  display: none;
  position: fixed;
  bottom: 5px;
  right: 20px;
  box-shadow: 0 0 10px rgba(0, 0, 0, .2);
}

#progress-value {
  height: calc(100% - 15px);
  width: calc(100% - 15px);
  background-color: var(--bg-main-color);
  color: var(--accent-color);
  border-radius: 50%;
  display: grid;
}

#progress-value:after {
  content: url("scrolltotop-img.6d1d1b59.png");
  animation: scrollTop 1s 1s infinite;
  position: absolute;
  bottom: 2px;
  right: 2px;
}

@keyframes scrollTop {
  0% {
    transform: translateY(0);
  }

  50% {
    transform: translateY(-5px);
  }

  100% {
    transform: translateY(0);
  }
}

.read-more-backdrop {
  z-index: 105;
  width: 100%;
  height: 100%;
  opacity: 1;
  background-color: rgba(0, 0, 0, .2);
  transition: opacity .4s cubic-bezier(.4, 0, .2, 1), visibility .4s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
}

.read-more-hidden {
