// F O O T E R
.footer {
    background: var(--bg-main-color);
  
    padding-bottom: 0px;
  
    @media screen and (min-width: 768px) {
      padding-bottom: 18px;
    }
    @media screen and (min-width: 1280px) {
      padding-bottom: 10px;
    }
  
    position: relative;
  }
  .footer::after {
    content: '';
  
    display: block;
  
    width: 100%;
    height: 1px;
  
    position: absolute;
    top: 320px;
  
    @media screen and (min-width: 768px) {
      top: 295px;
    }
    @media screen and (min-width: 1280px) {
      top: 278px;
    }
  
    background: #e1e1e1;
  }
  
  .footer-top {
    margin-bottom: 43px;
  
    @media screen and (min-width: 768px) {
      display: flex;
      margin-bottom: 120px;
    }
  
    @media screen and (min-width: 1280px) {
      margin-bottom: 106px;
    }
  }
  .footer-top_right {
    margin-left: auto;
  }
  
  // F O O T E R   S L O G A N
  .footer-slogan {
    max-width: 234px;
    margin-bottom: 43px;
  
    @media screen and (min-width: 768px) {
      max-width: 340px;
      margin-bottom: 0;
    }
    @media screen and (min-width: 1280px) {
      max-width: 474px;
    }
  }
  .footer-slogan_top {
    font-family: Titan One;
    font-weight: normal;
  
    font-size: 18px;
    line-height: 1.17;
  
    letter-spacing: 0.04em;
  
    text-transform: uppercase;
  
    color: var(--main-dark);
  
    margin-bottom: 17px;
  
    @media screen and (min-width: 768px) {
      font-size: 26px;
      line-height: 1.15;
  
      margin-bottom: 16px;
    }
    @media screen and (min-width: 1280px) {
      font-size: 30px;
      line-height: 1.13;
  
      margin-bottom: 15px;
    }
  }
  .footer-slogan_bottom {
    font-family: Titan One;
    font-weight: normal;
  
    font-size: 34px;
    line-height: 1.15;
  
    letter-spacing: 0.06em;
    text-transform: uppercase;
  
    color: var(--accent-color);
  
    @media screen and (min-width: 768px) {
      font-size: 48px;
      line-height: 1.145;
    }
    @media screen and (min-width: 1280px) {
      font-size: 58px;
      line-height: 1.14;
    }
  }
  
  // F O O T E R   S O C I A L
  .footer-social_list {
    margin-bottom: 46px;
  
    @media screen and (min-width: 1280px) {
      margin-bottom: 50px;
    }
  
    display: flex;
    justify-content: flex-end;
  }
  .footer-social_item:not(:last-child) {
    margin-right: 28px;
  
    @media screen and (min-width: 768px) {
      margin-right: 30px;
    }
    @media screen and (min-width: 1280px) {
      margin-right: 25px;
    }
  }
  .footer-social_link {
    border-radius: 50%;
    background: #fea5bb;
  
    transition: background 250ms cubic-bezier(0.4, 0, 0.2, 1);
  
    width: 32px;
    height: 32px;
  
    @media screen and (min-width: 768px) {
      width: 34px;
      height: 34px;
    }
  
    display: flex;
    justify-content: center;
    align-items: center;
  
    &:hover,
    &:focus {
      background: var(--accent-color);
    }
  }
  
  .footer-social_icon {
    width: 22px;
    height: 22px;
    fill: var(--standart-white);
  }
  
  // F O O T E R   C O N T A C T
  
  .footer-contact_link {
    font-family: DM Sans;
    font-weight: bold;
  
    font-size: 12px;
    line-height: 1.86;
  
    text-align: right;
  
    margin-bottom: 65px;
  
    @media screen and (min-width: 768px) {
      font-size: 14px;
      margin-bottom: 0;
    }
    @media screen and (min-width: 1280px) {
      font-size: 16px;
    }
  
    letter-spacing: 0.02em;
  
    color: var(--accent-color);
  
    display: block;
    margin-left: auto;
  }
  .footer-contact_text {
    color: #000000;
  }
  
  // F O O T E R   C O P Y R I G H T

  .footer-copyright {
    display: flex;
    justify-content: center;
  }
  .footer-copyright_text {
    font-family: DM Sans;
    font-weight: bold;
  
    font-size: 14px;
    line-height: 1.52;
  
    letter-spacing: 0.02em;
  
    color: var(--second-text-color);
  
    max-width: 150px;
  
    @media screen and (min-width: 768px) {
      font-size: 14px;
      line-height: 1.86;
  
      max-width: 100%;
    }
  }
